import axios from 'axios'

export function getHotSearch(latitude,longitude) {
    let url ='http://localhost:8080/static/1.json';
    let params = {latitude,longitude};
    return new Promise((resolve,reject)=>{
        axios.get(url,{params}).then((res)=>{
            resolve(res);
        }).catch((err)=>{
            reject(err);
        })
    }
    )    
}
export function getTemplate(latitude,longitude) {
    let url ='http://localhost:8080/static/2.json';
    let params = {latitude,longitude,'templates[]':'main-template'};
    return new Promise((resolve,reject)=>{
        axios.get(url,{params}).then((res)=>{
            resolve(res);
        }).catch((err)=>{
            reject(err);
        })
    }
    )   
}
export function getContent(latitude,longitude) {
    let url ='http://localhost:8080/static/3.json';
    let params = {latitude,longitude,'templates[]':'main-template'};
    return new Promise((resolve,reject)=>{
        axios.get(url,{params}).then((res)=>{
            resolve(res);
        }).catch((err)=>{
            reject(err);
        })
    }
    )   
}


