<template>
    <div id="star" :class="starType">
        <span class="elm-stars" :class="item" v-for="(item,index) in starClassas"></span>
    </div>
</template>
<script>
    const CLS_ON='on' //全亮
    const CLS_HALF='half' //半颗星
    const CLS_OFF='off'//不亮
    const LENGTH=5

    export default{
        props:{
            rating:Number,
            size:Number
        },
        computed:{
            starType(){
                return 'star-'+this.size
            },
            starClassas(){
                let rating =  Math.floor(this.rating*2)/2;
                let integer = Math.floor(rating);
                let remainder = rating % 1 ==0;
                let array = [];
                for (let i = 0; i < integer; i++) {
                    array.push(CLS_ON);
                    
                }
                if (remainder) {
                    array.push(CLS_OFF)
                }
                while (array.length < LENGTH) {
                    array.push(CLS_HALF)
                }
                return array
            }
        }
    }
</script>
<style lang="less" scoped>
    #star{font-size: 0}
    .elm-stars{display: inline-block;background-repeat: no-repeat;}
    .elm-stars{width: 10px;height: 10px;margin-left: 3px}
    .elm-stars.on{background: url('star24_on@2x.png');background-size: 10px 10px;}
    .elm-stars.half{background: url('star24_half@2x.png');background-size: 10px 10px;}
    .elm-stars.off{background: url('star24_off@2x.png');background-size: 10px 10px;}
</style>


