<template>
    <div>
        <elm-tabbar></elm-tabbar>
      <header>
          <div class="location">
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 26 31" class="index-okfdP_0">
                <path fill="#FFF" fill-rule="evenodd" d="M22.116 22.601c-2.329 2.804-7.669 7.827-7.669 7.827-.799.762-2.094.763-2.897-.008 0 0-5.26-4.97-7.643-7.796C1.524 19.8 0 16.89 0 13.194 0 5.908 5.82 0 13 0s13 5.907 13 13.195c0 3.682-1.554 6.602-3.884 9.406zM18 13a5 5 0 1 0-10 0 5 5 0 0 0 10 0z"></path>
              </svg>
              <span class="as">保定市河北软件职业技术学院</span>
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 14 8" class="index-2iXz3_0">
                <path fill="#FFF" fill-rule="evenodd" d="M5.588 6.588c.78.78 2.04.784 2.824 0l5.176-5.176c.78-.78.517-1.412-.582-1.412H.994C-.107 0-.372.628.412 1.412l5.176 5.176z"></path>
              </svg>
          </div>
      </header>
      <section class="search">
        <div class="button">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16" class="sskuang">
              <path fill-opacity=".38" d="M14.778 13.732a.739.739 0 1 1-1.056 1.036l-2.515-2.565a.864.864 0 0 1-.01-1.206 4.894 4.894 0 0 0 1.357-3.651c-.126-2.492-2.156-4.52-4.648-4.647a4.911 4.911 0 0 0-5.16 5.163c.126 2.475 2.13 4.496 4.605 4.642.451.026.896-.008 1.326-.1a.739.739 0 0 1 .308 1.446c-.56.12-1.137.164-1.72.13-3.227-.19-5.83-2.815-5.995-6.042a6.39 6.39 0 0 1 6.71-6.715c3.25.165 5.884 2.8 6.05 6.048a6.37 6.37 0 0 1-1.374 4.3l2.12 2.161z"></path>
            </svg>
            <span class="ass">搜索饿了么商家、商品名称</span>
        </div>
      </section>
      <section class="search1">
          <ul>
              <li v-for="(item,index) in hotsearch">{{item.productName}}</li>
          </ul>
      </section>
      <section class="entries">
        <mt-swipe :auto="4000">
            <mt-swipe-item v-for="(item,index) in templatas" :key="index">
                <a href="" v-for="(iten,imdex) in item" :key="imdex">
                    <img :src="iten.image_hash | entrias">
                    <span>{{iten.name}}</span>
                </a>
            </mt-swipe-item>
        </mt-swipe>
      </section>
      <section class="shop-title">
          推荐商家
      </section>
      <section class="shop-list">
          <section v-for="(item,index) in content">
            <div class="lists">
                <div class="imgs">
                    <img :src="item.restaurant.image_path | entrias">
                </div>
                <div class="content">
                    <div class="content1 cs">
                        <p>{{item.restaurant.name}}</p>
                        <span>···</span>
                    </div>
                    <div class="content2 cs">
                        <div class="css">
                            <elm-star :rating="item.restaurant.rating" :size="24"></elm-star>
                            <p>&nbsp;{{item.restaurant.rating}}&nbsp;</p>
                            <p>月售{{item.restaurant.recent_order_num}}单</p>
                        </div>
                        <p style="background-color: rgb(87, 145, 231);height:15px;line-height:.4rem;margin-top:8px">蜂鸟专送</p>
                    </div>
                    <div class="content3 cs">
                        <div class="css">
                            <p class="csss" style="height:15px;margin-top:8px;line-height:.365555rem;">￥{{item.restaurant.float_minimum_order_amount}}起送&nbsp;&nbsp;</p> 
                            <p style="height:15px;margin-top:8px;line-height:.365555rem;">&nbsp;&nbsp;起送费￥{{item.restaurant.float_delivery_fee}}</p>
                        </div>
                        <div class="css">
                            <p class="csss" style="height:15px;margin-top:8px;line-height:.365555rem;">{{item.restaurant.distance/1000}}km&nbsp;&nbsp;</p>
                            <p style="height:15px;margin-top:8px;line-height:.365555rem;">&nbsp;&nbsp;{{item.restaurant.order_lead_time}}分钟</p>
                        </div>
                    </div>
                    <div class="content3 cs">
                        <div>
                            <div v-for="(item,inder) in item.restaurant.activities">
                                <span style="background-color: rgb(230, 154, 15);height:20px;width:20px;line-height:.6rem;margin-top:4px;text-align: center">{{item.icon_name}}</span>
                                <span>&nbsp;&nbsp;{{item.description}}</span>
                            </div>
                        </div>
                        <p>{{item.restaurant.activities.length}}个活动</p>
                    </div>
                </div>
            </div>
        </section>
      </section>
    </div>
</template>     
<script>
    import Tabba from '@/components/footer/Tabba';
    import Star from '@/components/star/Star';
    import {getHotSearch,getTemplate,getContent} from '@/api/msite';
    import _ from 'lodash';
    import { Swipe, SwipeItem } from 'mint-ui';
    import "mint-ui/lib/style.css";
    import "mint-ui/lib/index.js";
    import Vue from "vue";
    Vue.component(Swipe.name, Swipe);
    Vue.component(SwipeItem.name, SwipeItem);
    export default{
        components:{
            'elm-tabbar':Tabba,
            'elm-star':Star
        },
        data(){
            return {
            hotsearch:[],
            templatas:[],
            content:[]
            }
        },
        created(){
            getHotSearch(40.1144418,116.3579348)
            .then((res)=>{
                // console.log(res.data)
                this.hotsearch=res.data.products;
                
            })
            .then(()=>{
                getTemplate(40.1144418,116.3579348).then((res)=>{
                    this.templatas = _.chunk(res.data[0].entries,8);
                    // console.log(this.templatas);                   
                })
            })
            .catch((err)=>{
                console.log(err)
            })
            getContent(40.1144418,116.3579348)
            .then((res)=>{
                console.log(res.data.items);
                this.content = res.data.items;
                // this.hotsearch=res.data.products;
                
            })
        },
        filters:{
            entrias(src){
               let htmlUrl = src.slice(0,1)+"/"+src.slice(1,3)+"/"+src.slice(3);
               let name = htmlUrl.match(/jpeg|jsp|png$/);
               return `https://fuss10.elemecdn.com/${htmlUrl}.${name[0]}?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/`;
            }
        }
    }
</script>      
<style lang="less" scoped>
    header{height: 1.4375rem; background-image: linear-gradient(90deg,#0af,#0085ff); display: flex; align-items: center;}
    .location{display: flex;align-items: center;}
    .index-okfdP_0{width: 0.475rem;height: 0.3125rem;margin-left: 7%}
    .index-2iXz3_0{width: 0.1875rem;height: 0.125rem;}
    .as{font-size: .45625rem;color: #000;width: 80%;white-space: nowrap;overflow: hidden;text-overflow: ellipsis;margin-left: 0.125rem;font-weight: 700;}
    
    .search{height: 1.4375rem; background-image: linear-gradient(90deg,#0af,#0085ff);display: flex;align-items: center;justify-content: center;position:sticky;top: 0;z-index: 999}
    .button{width: 90%;height: 0.9375rem;line-height: 0.4375rem;background: #fff}
    .search>a{display:flex;align-items: center;justify-content: center;text-emphasis: none;}
    .sskuang{width: 0.3125rem;height: 0.3125rem;margin-left: 1.23125rem}
    .ass{font-size: 0.3125rem;color: #999;}
    
    .search1{height: 1.0375rem;background-image:linear-gradient(90deg,#0af,#0085ff);width: 93.5%;padding: 0.0rem 0.32rem }
    ul{width: 95%;overflow-x:auto;white-space: nowrap;line-height: 10px;}
    ::-webkit-scrollbar{display: none}
    li{display: inline-block;font-size:.275rem;margin-left: .425rem;color: #fff;height: 15px}

    .entries{height: 4.3rem;width: 100%}
    .entries a{text-decoration: none;width: 25%;display: flex;flex-direction: column;align-content: center;float: left;margin-top: .15rem}
    .entries a span{font-size: .226667rem;text-align: center} 
    .entries a img{width: 1.286667rem;height: 1.266667rem;margin: 0 auto}

    .shop-title{font-size: .373333rem;display: flex; align-items: center;justify-content: center;height: .266667rem}
    .shop-title:before,.shop-title:after{content: "";display:block;width: .8rem;height: .026667rem;background: #000}
    .shop-title:before{margin-right: .3rem}
    .shop-title:after{margin-left: .3rem}

    .shopp-list{width: 100%;}
    .lists{width: 100%;height: 3.5rem;border-bottom: 1px solid #999;margin-top: 10px;overflow: hidden;}
    .imgs{width: 22%;height: 100%;margin-left: .266667rem;float: left}
    .imgs img{width: 100%;background: yellow;margin-top: .213333rem}
    .content{width: 71%;height: 100%;float: left;margin-left: 6px}
    .cs{display: flex;justify-content: space-between;width: 100%;height: .8rem;font-size: .3rem;line-height: .8rem}
    .css{display: flex;font-size: .3rem;line-height: .8rem}
    .content1 p{font-weight: bold;font-size: .4rem }
    .csss{border-right: 1px solid #000;}
    .lists div{height:.666667rem}
</style>


