 <template>
    <footer>
        <div>
            <a @click="dianji('/home')">
                <svg class="index-footerTabIcon_1EbB8wS"><use xlink:href="#h5-component-index"></use></svg>
                <span>首页</span>
            </a>
            <a @click="dianji('/discover')">
                <svg class="index-footerTabIcon_1EbB8wS"><use xlink:href="#h5-component-discoverRegular"></use></svg>
                <span>发现</span>
            </a>
            <a @click="dianji('/order')">
                <svg class="index-footerTabIcon_1EbB8wS"><use xlink:href="#h5-component-orderRegular"></use></svg>
                <span>订单</span>
            </a>
            <a @click="dianji('/profile')">
                <svg class="index-footerTabIcon_1EbB8wS"><use xlink:href="#h5-component-profile"></use></svg>
                <span>我的</span>
            </a>
        </div>
    </footer>
 </template>
 <script>
     export default{
         methods:{
             dianji(path){
                this.$router.push({path: path});
             }
         }
     }
 </script>
 <style lang="less" scoped>
     footer{
         width: 100%;
         height: 1.425rem;
         position: fixed;
         bottom: 0;
         left: 0;
         background: #fff;
         box-shadow: 0 -0.25vw 0.5625vw rgba(0, 0, 0,.1);
         z-index: 999;
         background: #fff
     }
     div{
         width: 100%;
         display: flex;
         justify-content:space-around;
         align-items: center;        
     }
     svg{
         width: 0.5333333rem;
         height: 0.5333333rem;
     }
     span{
         font-size: .25rem;
         display: block
     }
 </style>


